          <div class="setting-item">
            <label>Ë¥®Èáè:</label>
            <el-select v-model="quality" size="small">
              <el-option label="È´òË¥®Èáè" value="high"></el-option>
              <el-option label="‰∏≠Á≠âË¥®Èáè" value="medium"></el-option>
              <el-option label="‰ΩéË¥®Èáè" value="low"></el-option>
            </el-select>
