          @click="startConversion"
          :loading="converting"
          icon="el-icon-video-camera"
        >
          {{ converting ? 'ËΩ¨Êç¢‰∏≠...' : 'ÂºÄÂßãËΩ¨Êç¢' }}
        </el-button>
      </div>

      <!-- ËΩ¨Êç¢ËøõÂ∫¶ -->
      <div v-if="conversionProgress.show" class="progress-section">
        <h3>ËΩ¨Êç¢ËøõÂ∫¶</h3>
        <el-progress
          :percentage="conversionProgress.percentage"
          :status="conversionProgress.status || null"
          :stroke-width="8"
        ></el-progress>
        <div class="progress-info">
          <span>{{ conversionProgress.text }}</span>
          <span v-if="conversionProgress.eta">È¢ÑËÆ°Ââ©‰Ωô: {{ conversionProgress.eta }}</span>
        </div>
      </div>

      <!-- ÁªìÊûúÂ±ïÁ§∫ -->
      <div v-if="gifResult" class="result-section">
        <h3>ËΩ¨Êç¢ÁªìÊûú</h3>
        <div class="result-container">
          <div class="gif-preview">
            <img :src="gifResult.url" alt="ÁîüÊàêÁöÑGIF" />
          </div>
          <div class="result-info">
            <div>Êñá‰ª∂Â§ßÂ∞è: {{ formatFileSize(gifResult.size) }}</div>
            <div>Â∞∫ÂØ∏: {{ gifResult.width }}√ó{{ gifResult.height }}</div>
            <div>Â∏ßÊï∞: {{ gifResult.frames }}</div>
            <div>Êó∂Èïø: {{ formatTime(endTime - startTime) }}</div>
          </div>
          <div class="result-actions">
            <el-button type="success" @click="downloadGif">
              <i class="el-icon-download"></i>
              ‰∏ãËΩΩGIF
            </el-button>
            <el-button @click="resetConverter">
              <i class="el-icon-refresh"></i>
              ÈáçÊñ∞ËΩ¨Êç¢
            </el-button>
          </div>
        </div>
      </div>

      <!-- ‰ΩøÁî®ËØ¥Êòé -->
      <div class="usage-guide">
        <h3>‰ΩøÁî®ËØ¥Êòé</h3>
        <div class="guide-content">
          <div class="guide-section">
            <h4>ËΩ¨Êç¢Ê≠•È™§</h4>
            <ol>
              <li>‰∏ä‰º†ÈúÄË¶ÅËΩ¨Êç¢ÁöÑËßÜÈ¢ëÊñá‰ª∂</li>
              <li>ËÆæÁΩÆGIFÁöÑÂºÄÂßãÂíåÁªìÊùüÊó∂Èó¥</li>
              <li>Ë∞ÉÊï¥GIFÁöÑÂ∞∫ÂØ∏„ÄÅÂ∏ßÁéáÂíåË¥®Èáè</li>
              <li>ÁÇπÂáª"ÂºÄÂßãËΩ¨Êç¢"ÁîüÊàêGIF</li>
              <li>‰∏ãËΩΩÁîüÊàêÁöÑGIFÊñá‰ª∂</li>
