              <li><strong>Êó∂Èó¥ËåÉÂõ¥</strong>ÔºöÈÄâÊã©ËßÜÈ¢ë‰∏≠Ë¶ÅËΩ¨Êç¢ÁöÑÁâáÊÆµ</li>
              <li><strong>ÂÆΩÂ∫¶</strong>ÔºöGIFÁöÑÂÆΩÂ∫¶ÔºåÈ´òÂ∫¶‰ºöÊåâÊØî‰æãÁº©Êîæ</li>
              <li><strong>Â∏ßÁéá</strong>ÔºöÊØèÁßíÂ∏ßÊï∞ÔºåÂΩ±ÂìçÊµÅÁïÖÂ∫¶ÂíåÊñá‰ª∂Â§ßÂ∞è</li>
              <li><strong>Ë¥®Èáè</strong>ÔºöÂéãÁº©Ë¥®ÈáèÔºåÂΩ±ÂìçÊ∏ÖÊô∞Â∫¶ÂíåÊñá‰ª∂Â§ßÂ∞è</li>
            </ul>
          </div>
          
          <div class="guide-section">
            <h4>‰ºòÂåñÂª∫ËÆÆ</h4>
            <ul>
              <li>Âª∫ËÆÆGIFÊó∂ÈïøÊéßÂà∂Âú®10Áßí‰ª•ÂÜÖ</li>
              <li>ËæÉ‰ΩéÁöÑÂ∏ßÁéáÂèØ‰ª•ÂáèÂ∞èÊñá‰ª∂Â§ßÂ∞è</li>
              <li>ÈÄÇÂΩìÈôç‰ΩéÂÆΩÂ∫¶ÂèØ‰ª•ÊòæËëóÂáèÂ∞èÊñá‰ª∂</li>
              <li>ÈÄâÊã©ÂêàÈÄÇÁöÑË¥®ÈáèÂπ≥Ë°°Ê∏ÖÊô∞Â∫¶ÂíåÂ§ßÂ∞è</li>
            </ul>
          </div>
        </div>
      </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'VideoToGifView',
  metaInfo: {
    title: 'ËßÜÈ¢ëËΩ¨GIF - ÂÖçË¥πÂú®Á∫øËßÜÈ¢ëËΩ¨GIFÂ∑•ÂÖ∑ÔºåÊîØÊåÅMP4/MOV/WebM',

    meta: [
      // SEO Âü∫Á°Ä
      {
        name: 'description',
        content: 'ÂÖçË¥πÂú®Á∫øËßÜÈ¢ëËΩ¨GIFÂ∑•ÂÖ∑ÔºåÊîØÊåÅMP4„ÄÅMOV„ÄÅWebMÁ≠âÊ†ºÂºè‰∏ÄÈîÆËΩ¨Êç¢ÔºåÈ´òÊ∏ÖÊó†Ê∞¥Âç∞ÔºåÊîØÊåÅËá™ÂÆö‰πâÂ∏ßÁéá„ÄÅÂ∞∫ÂØ∏„ÄÅÂæ™ÁéØÊ¨°Êï∞ÔºåÁßíÈÄüÂØºÂá∫GIFÂä®ÂõæÔºÅ',
        vmid: 'description'
      },
      {
        name: 'keywords',
        content: 'ËßÜÈ¢ëËΩ¨gif,Âú®Á∫øËßÜÈ¢ëËΩ¨gif,mp4ËΩ¨gif,movËΩ¨gif,ÂÖçË¥πËßÜÈ¢ëËΩ¨gifÂ∑•ÂÖ∑,ËßÜÈ¢ëÊèêÂèñgif,Êó†ÊçüËΩ¨Êç¢',
        vmid: 'keywords'
      },

      // ÂæÆ‰ø°„ÄÅQQ„ÄÅÂæÆÂçöÂàÜ‰∫´Âç°ÁâáÔºàOpen GraphÔºâ
      {
        property: 'og:title',
        content: 'ËßÜÈ¢ëËΩ¨GIF - ÂÖçË¥πÂú®Á∫øËΩ¨Êç¢Â∑•ÂÖ∑ | 4YOPÂ∑•ÂÖ∑ÁÆ±',
        vmid: 'og:title'
      },
      {
        property: 'og:description',
        content: '‰∏ÄÈîÆÊääËßÜÈ¢ëËΩ¨ÊàêÈ´òÊ∏ÖGIFÔºåÊîØÊåÅËá™ÂÆö‰πâÂ∏ßÁéáÂíåÂ∞∫ÂØ∏ÔºåÂÆåÂÖ®ÂÖçË¥πÊó†ÂπøÂëäÔºÅ',
        vmid: 'og:description'
      },
      {
        property: 'og:image',
        content: 'https://tool.4yop.com/og-video-to-gif.jpg',  // Âª∫ËÆÆ‰Ω†‰∏ä‰º†‰∏ÄÂº† 1200√ó630 ÁöÑÈ¢ÑËßàÂõæ
        vmid: 'og:image'
      },
      {
        property: 'og:url',
        content: 'https://tool.4yop.com/video-to-gif',
        vmid: 'og:url'
      },
      {
        property: 'og:type',
        content: 'website',
        vmid: 'og:type'
      },

      // Twitter CardÔºàÊé®ÁâπÂàÜ‰∫´Ôºâ
      {
        name: 'twitter:card',
        content: 'summary_large_image',
        vmid: 'twitter:card'
      },
      {
        name: 'twitter:title',
        content: 'ËßÜÈ¢ëËΩ¨GIF - ÂÖçË¥πÂú®Á∫øËΩ¨Êç¢Â∑•ÂÖ∑ | 4YOPÂ∑•ÂÖ∑ÁÆ±',
        vmid: 'twitter:title'
      },
      {
        name: 'twitter:description',
        content: '‰∏ÄÈîÆÊääËßÜÈ¢ëËΩ¨ÊàêÈ´òÊ∏ÖGIFÔºåÊîØÊåÅËá™ÂÆö‰πâÂ∏ßÁéáÂíåÂ∞∫ÂØ∏ÔºåÂÆåÂÖ®ÂÖçË¥πÊó†ÂπøÂëäÔºÅ',
        vmid: 'twitter:description'
      },
      {
        name: 'twitter:image',
        content: 'https://tool.4yop.com/og-video-to-gif.jpg',
        vmid: 'twitter:image'
      }
    ]
  },
  data() {
    return {
      videoFile: null,
      videoUrl: '',
      videoDuration: 0,
      startTime: 0,
      endTime: 5,
      gifWidth: 480,
      frameRate: 15,
      quality: 'medium',
      converting: false,
      conversionProgress: {
        show: false,
        percentage: 0,
        status: '',
        text: '',
        eta: ''
      },
      gifResult: null,
      
      // ÊãñÊãΩÁõ∏ÂÖ≥
      dragging: null,
      dragStartX: 0,
      dragStartValue: 0
    }
  },
  computed: {
    estimatedSize() {
      if (!this.videoFile) return 'Êú™Áü•'
      
      const duration = this.endTime - this.startTime
      const frames = Math.ceil(duration * this.frameRate)
      const pixelCount = this.gifWidth * (this.gifWidth * 0.75) // ÂÅáËÆæ16:9ÊØî‰æã
      
      let bytesPerPixel = 1
      switch (this.quality) {
        case 'high': bytesPerPixel = 1.5; break
        case 'medium': bytesPerPixel = 1; break
        case 'low': bytesPerPixel = 0.5; break
      }
      
      const estimatedBytes = frames * pixelCount * bytesPerPixel
      return this.formatFileSize(estimatedBytes)
    }
  },
  methods: {
    triggerFileInput() {
      this.$refs.fileInput.click()
    },

    handleFileSelect(event) {
      const file = event.target.files[0]
      if (file) {
        this.loadVideo(file)
      }
    },

    handleDrop(event) {
      event.preventDefault()
      const file = event.dataTransfer.files[0]
      if (file && file.type.startsWith('video/')) {
        this.loadVideo(file)
      }
    },

    loadVideo(file) {
      this.videoFile = file
      this.videoUrl = URL.createObjectURL(file)
      this.resetConverter()
    },

    onVideoLoaded() {
      const video = this.$refs.videoElement
      this.videoDuration = video.duration
      this.endTime = Math.min(5, this.videoDuration)
    },

    onTimeUpdate() {
      // ÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†Êó∂Èó¥Êõ¥Êñ∞ÁöÑÈÄªËæë
    },

    updateTimeRange() {
      // Á°Æ‰øùÊó∂Èó¥ËåÉÂõ¥ÊúâÊïà
      if (this.startTime >= this.endTime) {
        this.startTime = Math.max(0, this.endTime - 0.1)
      }
      if (this.endTime <= this.startTime) {
        this.endTime = Math.min(this.videoDuration, this.startTime + 0.1)
      }
    },

    startDrag(handle, event) {
      this.dragging = handle
      this.dragStartX = event.clientX
      this.dragStartValue = handle === 'start' ? this.startTime : this.endTime
      
      document.addEventListener('mousemove', this.onDrag)
      document.addEventListener('mouseup', this.stopDrag)
      event.preventDefault()
    },

    onDrag(event) {
      if (!this.dragging) return
      
      const deltaX = event.clientX - this.dragStartX
      const timelineWidth = 300 // ÂÅáËÆæÊó∂Èó¥ËΩ¥ÂÆΩÂ∫¶
      const deltaTime = (deltaX / timelineWidth) * this.videoDuration
      
      if (this.dragging === 'start') {
        this.startTime = Math.max(0, Math.min(this.endTime - 0.1, this.dragStartValue + deltaTime))
      } else {
        this.endTime = Math.min(this.videoDuration, Math.max(this.startTime + 0.1, this.dragStartValue + deltaTime))
      }
    },

    stopDrag() {
      this.dragging = null
      document.removeEventListener('mousemove', this.onDrag)
      document.removeEventListener('mouseup', this.stopDrag)
    },

    async startConversion() {
      this.converting = true
      this.conversionProgress.show = true
      this.conversionProgress.percentage = 0
      this.conversionProgress.status = ''
      
      try {
        await this.simulateConversion()
        this.conversionProgress.status = 'success'
        this.$message.success('GIFËΩ¨Êç¢ÂÆåÊàêÔºÅ')
      } catch (error) {
        this.conversionProgress.status = 'exception'
        this.$message.error('ËΩ¨Êç¢Â§±Ë¥•ÔºåËØ∑ÈáçËØï')
      } finally {
        this.converting = false
      }
    },

    async simulateConversion() {
      const duration = this.endTime - this.startTime
      const totalFrames = Math.ceil(duration * this.frameRate)
      
      for (let i = 0; i <= totalFrames; i++) {
        this.conversionProgress.percentage = Math.round((i / totalFrames) * 100)
        this.conversionProgress.text = `Â§ÑÁêÜÂ∏ß ${i}/${totalFrames}`
        
        const remainingFrames = totalFrames - i
        const eta = Math.ceil(remainingFrames * 0.1) // ÂÅáËÆæÊØèÂ∏ß0.1Áßí
        this.conversionProgress.eta = eta > 0 ? `${eta}Áßí` : ''
        
        await new Promise(resolve => setTimeout(resolve, 100))
      }
      
      // Ê®°ÊãüÁîüÊàêÁªìÊûú
      this.gifResult = {
        url: this.videoUrl, // ÂÆûÈôÖÂ∫îËØ•ÊòØÁîüÊàêÁöÑGIF URL
        size: Math.random() * 5000000 + 1000000, // 1-6MB
        width: this.gifWidth,
        height: Math.round(this.gifWidth * 0.75),
        frames: totalFrames
      }
    },

    downloadGif() {
      // ÂÆûÈôÖÂÆûÁé∞‰∏≠Â∫îËØ•‰∏ãËΩΩÁúüÊ≠£ÁöÑGIFÊñá‰ª∂
      const link = document.createElement('a')
      link.href = this.gifResult.url
      link.download = `video-to-gif-${Date.now()}.gif`
      link.click()
      this.$message.success('ÂºÄÂßã‰∏ãËΩΩGIFÊñá‰ª∂')
    },

    resetConverter() {
      this.gifResult = null
      this.conversionProgress.show = false
      this.startTime = 0
      this.endTime = Math.min(5, this.videoDuration || 5)
    },

    formatTime(seconds) {
      const mins = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${mins}:${secs.toString().padStart(2, '0')}`
    },

    formatFileSize(bytes) {
      if (bytes === 0) return '0 Bytes'
      const k = 1024
      const sizes = ['Bytes', 'KB', 'MB', 'GB']
      const i = Math.floor(Math.log(bytes) / Math.log(k))
      return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
  },
  
  beforeDestroy() {
    if (this.videoUrl) {
      URL.revokeObjectURL(this.videoUrl)
    }
  }
}
</script>

